
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#mainDiv {
  border-color: indigo;
}

h1, h2{
  color: green;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;

}

#searchDiv{
  font-style: italic;
}

button {
  border: none;
  color: white;
  text-align: center;
  display: inline-block;
}

#addButton {
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
#addButton:hover{
  background-color: greenyellow;
}

#deleteButton {
  background-color: red;
  padding: 3px 3px;
  font-size: 11px;
}
#deleteButton:hover{
  padding: 4px 5px;
}

input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

li{
  font-size: 16px;
  margin: 5px;
  font-family: 'Courier New', Courier, monospace;
}

li:hover{
  font-size: 22px ;
}

#addForm {
  font-style: italic;
}

#contactList{
  background-color: rgb(213, 241, 218);
}